const Folder = ({ folder, onFolderChoice, isLoading, isHidden }) => {
  const handleClick = () => {
    onFolderChoice(folder);
  };

  return (
    <li
      onClick={handleClick}
      className={`folderItem ${isLoading ? "disabled" : ""} ${isHidden ? "hiddenFolder" : ""}`}
    >
      {folder.name.replace(".zip", "")}
    </li>
  );
};

export default Folder;
