import { useState } from "react";
import Folders from "./components/Folders";
import Posts from "./components/Posts";
import useFolderChoice from "./hooks/ChooseFolder";
import Pagination from "./components/Pagination";
import "./style.css";

const Home = () => {
  const [data, setData] = useState(null);
  const [activeFolder, setActiveFolder] = useState(null);
  const [page, setPage] = useState(0);
  const { isLoading, progress, handleFolderChoice } = useFolderChoice();

  return (
    <div>
      {isLoading && (
        <div
          className="loader"
          style={{ width: `${progress / 5}rem`, height: `${progress / 5}rem` }}
        >
          <p>{progress}%</p>
        </div>
      )}

      <Folders
        onFolderChoice={(folder) =>
          handleFolderChoice(folder, setData, setActiveFolder)
        }
        isHidden={!!data}
        isLoading={isLoading}
      />
      {data && (
        <>
          <h2 className="folderTitle">{activeFolder}</h2>
          <Pagination page={page} setPage={setPage} totalPosts={data?.length} />
          <Posts data={data?.slice(page * 6, (page + 1) * 6)} />
        </>
      )}
    </div>
  );
};

export default Home;
