import JSZip from "jszip";

function fetchAndUnzipWithProgress(url, onProgress) {
  return new Promise((resolve, reject) => {
    fetch(url)
      .then((response) => {
        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }

        const contentLength = +response.headers.get("content-length");
        let receivedLength = 0;

        const reader = response.body.getReader();
        const chunks = [];

        function readChunk() {
          reader
            .read()
            .then(({ done, value }) => {
              if (done) {
                const concatenatedChunks = new Uint8Array(receivedLength);
                let position = 0;
                for (const chunk of chunks) {
                  concatenatedChunks.set(chunk, position);
                  position += chunk.length;
                }

                // Use JSZip to extract the contents of the ZIP file
                JSZip.loadAsync(concatenatedChunks)
                  .then((zip) => {
                    const files = [];
                    zip.forEach((relativePath, zipEntry) => {
                      if (!zipEntry.dir) {
                        // Exclude directories
                        files.push({
                          name: zipEntry.name,
                          data: zipEntry.async("string"),
                        });
                      }
                    });
                    return Promise.all(files.map((file) => file.data));
                  })
                  .then((fileContents) => {
                    resolve(fileContents);
                  })
                  .catch(reject);

                return;
              }

              chunks.push(value);
              receivedLength += value.length;

              if (onProgress) {
                onProgress(receivedLength, contentLength);
              }

              readChunk();
            })
            .catch(reject);
        }

        readChunk();
      })
      .catch(reject);
  });
}

export default fetchAndUnzipWithProgress;
