import { useState } from "react";
import fetchAndUnzip from "../utils/fetchAndUnzip";

const ChooseFolder = () => {
  const [isLoading, setIsLoading] = useState(false);
  const [progress, setProgress] = useState(0);

  const handleFolderChoice = async (folder, setData, setActiveFolder) => {
    if (isLoading) return;
    setProgress(0);
    setIsLoading(true);

    try {
      const zippedData = await fetchAndUnzip(
        folder.download_url,
        (received, total) => {
          const progress = (received / total) * 100;
          setProgress(Math.round(progress));
        },
      );

      setActiveFolder(folder.name);
      const dataObject = zippedData.map((item) => JSON.parse(item));
      const data = dataObject.flat();
      setData(data);
    } catch (error) {
      console.error("Error fetching data:", error);
    } finally {
      setIsLoading(false);
    }
  };

  return { isLoading, progress, handleFolderChoice };
};

export default ChooseFolder;
