import React, { useEffect, useState } from "react";
import Folder from "./Folder";

const RepositoryFolders = ({ onFolderChoice, isHidden, isLoading }) => {
  const [folders, setFolders] = useState([]);
  const owner = "Webhose";
  const repo = "free-news-datasets";
  const path = "News_Datasets";

  const fetchFolders = async () => {
    try {
      const encodedPath = encodeURIComponent(path);
      const url = `https://api.github.com/repos/${owner}/${repo}/contents/${encodedPath}`;
      const response = await fetch(url);
      const data = await response.json();
      setFolders(data);
    } catch (error) {
      console.error("Error fetching folders:", error);
    }
  };

  useEffect(() => {
    fetchFolders();
  }, []);

  return (
    <div>
      <ul className={`FoldersContainer ${isHidden ? "hidden" : ""}`}>
        {folders.length > 0 ? (
          folders.map((folder) => (
            <Folder
              key={folder.name}
              folder={folder}
              onFolderChoice={onFolderChoice}
              isLoading={isLoading}
              isHidden={isHidden}
            />
          ))
        ) : (
          <li>No folders found</li>
        )}
      </ul>
    </div>
  );
};

export default RepositoryFolders;
