import { useState, useEffect } from "react";

const Pagination = ({ page, setPage, totalPosts }) => {
  useEffect(() => {
    setPage(0);
  }, []);
  return (
    <div className="pagination">
      <div>
        <h3>
          {page * 6 + 1} - {(page + 1) * 6} of {totalPosts} posts
        </h3>
      </div>
      <div>
        <button
          className={`paginationButton ${page === 0 && "disabled"}`}
          onClick={() => {
            if (page > 0) {
              setPage((prev) => prev - 1);
            }
          }}
        >
          Prev
        </button>
        <button
          className={`paginationButton ${page === Math.ceil(totalPosts / 6) - 1 && "disabled"}`}
          onClick={() => {
            if (page < Math.ceil(totalPosts / 6) - 1) {
              setPage((prev) => prev + 1);
            }
          }}
        >
          Next
        </button>
      </div>
    </div>
  );
};

export default Pagination;
