const Posts = ({ data }) => {
  return (
    <ul className="appContainer">
      {data &&
        data.map((item) => (
          <li key={item.uuid} className="listItem">
            <span className="imageAndBeacon">
              <div className={`beacon ${item.sentiment}`} />
              {item.thread.main_image ? (
                <img
                  onError={(e) => {
                    e.target.src = "/image-error.svg";
                  }}
                  src={item.thread.main_image}
                  className="image"
                  alt={item.title}
                  title={item.title}
                />
              ) : (
                <div className="noImage" />
              )}
            </span>
            <div className="head">
              <h2 className="title" title={item.title}>
                {item.title}
              </h2>

              <div className="infoSection">
                {[
                  {
                    label: "published at",
                    value: item.published,
                    type: "date",
                  },
                  { label: "categories", value: item.categories.join(", ") },
                  {
                    label: "replies count",
                    value: item.thread.replies_count,
                  },
                  {
                    label: "language",
                    value: item.language,
                  },
                  {
                    label: "participants count",
                    value: item.thread.participants_count,
                  },
                  { label: "domain rank", value: item.thread.domain_rank },
                  { label: "URL", value: item.thread.url, type: "link" },
                ].map((info) => (
                  <p className="info" key={info.label} title={info.value}>
                    {info.label}:{" "}
                    <a
                      target="_blank"
                      {...(info.type === "link" ? { href: info.value } : {})}
                    >
                      {info.type === "date"
                        ? new Date(info.value).toLocaleString()
                        : info.value}
                    </a>
                  </p>
                ))}
              </div>
            </div>
            <p className="text">{item.text}</p>
          </li>
        ))}
    </ul>
  );
};

export default Posts;
